import "./App.css";

import {
  useRoutes,
  RouterProvider,
  createHashRouter,
  createBrowserRouter as Router,
  Navigate,
  Routes,
  Route,
  useNavigate,
} from "react-router-dom";
import Appardetails from "./component/Appardetails";
import Rootlayout from "./component/Rootlayout";
import Home from "./component/Home";
import Bolgs from "./pages/Bolgs";
import Rent from "./pages/Rent";
import { Sell } from "@mui/icons-material";
import { useEffect } from "react";
import { useMatch } from "react-router-dom";

const routerx = Router([
  {
    path: "/",
    element: <Rootlayout />,
    children: [
      { index: true, element: <Home /> },
      { path: "blog", element: <Bolgs /> },
      { path: "rent", element: <Rent /> },
      { path: "sell", element: <Rent /> },
      { path: ":a/:b/:c/:d/:e/:id", element: <Appardetails /> },
      { path: "*", element: <Navigate to={"/"} /> },
    ],
  },
]);

function App() {
  return (
    <>
      <RouterProvider router={routerx} />
    </>
  );
}

export default App;
